"use client"
import { reviews } from "@/lib/data";

export default function HappyCustomer({ reviews }) {
    console.log(reviews)
    return (
        <div>
            <div className="w-[70%] mx-auto">
                <h1 className="text-3xl font-extrabold">OUR HAPPY CUSTOMERS</h1>
                {reviews.reviews.map(rv => {
                    <div key={rv.id}>
                        <p>{rv.rating}</p>
                        <p>{rv.name}</p>
                        <p>{rv.review}</p>
                    </div>
                })}
            </div>
        </div>
    )
}